
:root {
  /*สีที่ต้องใช้ในโปรเจค 😊*/
  --primary-color: #387ADF; /*สีหลัก*/
  --secondary-color: #50C4ED; /*สีรอง*/
  --accent-color:#FBA834; /*สีที่เด่น*/
  --text-color:#020A0D; /*สีอักษร*/
  --bg-color:#f3f9fc; /*สีพื้นหลัง*/
  --primary60-color: #82C3DE; /*สีหลัก ลด ความเข้มลง 60%*/
  --primary20-color: #C4D9E2; /*สีหลัก ลด ความเข้มลง 20%*/
  --border-color: #D4D4D4; /*สีกรอบ */
  --color-white:#ffffff; /*สีพื้นหลังขาว*/
  --color-line:#00C300; /*สีเขียว line*/

  



/* สีปุ่มprimary เมื่อ hover*/
--primary-color-hover:#6b9ce7;
/* สีปุ่มprimary เมื่อกด hover*/
--primary-color-active: #428cfb;


--secondary-color-hover: #68d2f8; 
--secondary-color-active: #5cbfe3;
/* สีปุ่ม secondary เมื่อกด bottom*/
--secondary-color-bottom:#44a5c8;

  /*ขนาดอักษรที่ใช้ในโปรเจค ✨*/
  /*Heading ✅*/
  --font-size-h1-light:93px; 
  --font-size-h2-light:58px;
  --font-size-h3-regular:46px;
  --font-size-h4-regular:33px;
  --font-size-h5-regular:23px;
  --font-size-h6-medium:19px;  

  /*Subtitle ✅*/
  --font-size-s1-regular:15px;
  --font-size-s2-medium:13px;

   /*Paragraph ✅*/
  --font-size-p1-regular:15px;
  --font-size-p2-regular:13px;

  /*Button ✅*/
  --font-size-b1-bold:16px;
  --font-size-b2-semibold:14px;
  --font-size-b3-semibold:12px;
  /*Label ✅*/
  --font-size-l1-semibold:15px;
  --font-size-l2-semibold:10px;

  /* Custom Size ✅ */
  --font-size-32-custom: 32px;  /* ขนาด 12px สำหรับใช้งานทั่วไป */
  --font-size-24-custom: 24px;
  --font-size-19-custom: 19px;  /* ขนาด 16px สำหรับใช้งานทั่วไป */
  --font-size-18-custom: 18px;  /* ขนาด 18px สำหรับใช้งานทั่วไป */
--font-size-16-custom: 16px;  /* ขนาด 16px สำหรับใช้งานทั่วไป */
--font-size-14-custom: 14px;  /* ขนาด 14px สำหรับใช้งานทั่วไป */
--font-size-12-custom: 12px;  /* ขนาด 12px สำหรับใช้งานทั่วไป */
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Prompt", sans-serif;  /* กํานหนด font*/
  background: var(--bg-color); /*กําหนดสีพื้นหลังทั้งหมด*/
  color: var(--text-color); /*กําหนดสี ตัวอักษรเริ่มต้น*/
  
}

a{
  text-decoration: none; /*ลบเส้นใต้*/
  color: var(--text-color);
}

p{
   margin: 0px;
   
}


h1{
  margin: 0px;
}
h2{
  margin: 0px;
}
h4{
  margin: 0px;
}


h5{
  margin: 0px;
}
h6{
  margin: 0px;
}